.newArticlesList {
    margin-top: 40px;
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px 30px;
}

@media (max-width: 992px) and (min-width: 576px) {
    .newArticlesList {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (min-width: 992px) {
    .newArticlesList {
        grid-template-columns: repeat(3, 1fr);
    }
}

.addMoreBtn_container {
    text-align: center;
    margin: 30px 0;
}

.addMoreBtn {
    font-family: 'Lato', sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.05em;
    color: white;
    padding: 10px 20px;
    border: none;
    background: #78716c;
    border-radius: 10px;
}